import React, { useEffect, useState } from "react";
import { useIsMutating } from "@tanstack/react-query";
import { useNavigate, useParams } from "react-router-dom";
import { Button, FormGroup, Input } from "mtforms";
import { errorAlert, successAlert } from "../../../utils";
import { PublicPaths } from "../../../routes/path";
import PasswordInput from "../../../components/Forms/PasswordInput";
import { useResetPassword } from "./hooks";

const ResetPassword = () => {
	const navigate = useNavigate();
	const [formData, setFormData] = useState({});
	const [errors, setErrors] = useState({});
	const {
		mutate,
		isError,
		isSuccess,
		error: resetPasswordError,
		reset,
	} = useResetPassword();
	const isLoading = useIsMutating();

	const handleChange = (name, value) => {
		setFormData({ ...formData, [name]: value });
	};

	const validationHandler = (name, error) => {
		setErrors({ ...errors, [name]: error });
	};

	const {id} = useParams();

	const submitHandler = () => {
		const data = { ...formData, token: id };
		
		 mutate(data);
	};

	useEffect(() => {
		if (isSuccess) {
			reset();
			successAlert("Password Reset Successfully!");
			navigate(PublicPaths.ADMIN_LOGIN);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [isSuccess]);

	if (isError) {
		reset();
		errorAlert(resetPasswordError);
	}

	return (
		<>
			<h1>Reset Password</h1>
			<FormGroup onSubmit={submitHandler}>
				<Input
					name="email"
					label="Email Address"
					value={formData["email"]}
					onChange={handleChange}
					type="email"
					validationHandler={validationHandler}
					error={errors.email}
					required={true}
					size="large"
				/>

				{/* <Input
					name="token"
					label="Enter token"
					value={formData["token"]}
					onChange={handleChange}
					type="text"
					validationHandler={validationHandler}
					error={errors.token}
					required={true}
					size="large"
				/> */}

				<PasswordInput
					name="password"
					label="Password"
					value={formData["password"]}
					onChange={handleChange}
					validationHandler={validationHandler}
					error={errors.password}
					required={true}
					size="large"
				/>
				<PasswordInput
					name="confirmPassword"
					label="Confirm Password"
					value={formData["confirmPassword"]}
					onChange={handleChange}
					validationHandler={validationHandler}
					error={errors.confirmPassword}
					required={true}
					size="large"
				/>

				<Button
					title="reset password"
					size="large"
					className={isLoading ? "btn-disabled" : ""}
					loading={isLoading === 1}
					disabled={isLoading === 1}
				/>
			</FormGroup>
		</>
	);
};

export default ResetPassword;
